import {Row, Col, Card, CardBody, Modal} from "reactstrap"
import React, {useEffect, useState} from "react"
import ModalProduct from "./modal/ModalProduct"
import imgEmpty from "../../../../assets/images/icons/icons/ic_upload_img.svg"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import {DragDrop} from "@uppy/react"
import icDelete from "../../../../assets/images/icons/icons/ic_delete.svg"


const WardrobeCollection = props => {

    const {wardrobe, products} = props
    const [backgroundImage, setBackgroundImage] = useState(null)
    const [modalOpen, setModalIsOpen] = useState(false)
    const [index, setIndex] = useState(0)
    const [wardrobeDetails, setWardrobeDetails] = useState([])

    useEffect(() => {
        if (wardrobe?.id && wardrobe?.wardrobe_details) {
            setWardrobeDetails(wardrobe?.wardrobe_details)
            setBackgroundImage(wardrobe?.collection_url_thumb)
        }
    }, [])

    function deleteImage(e, wardrobeImageDetail) {
        wardrobeDetails.splice(wardrobeDetails.indexOf(wardrobeImageDetail), 1)
        setWardrobeDetails([...wardrobeDetails])
    }

    const convertImage = (index) => {
        let imageUrl = imgEmpty
        let wardrobeImageDetail = {}
        wardrobeDetails?.forEach(wardrobeDetail => {
            if (wardrobeDetail?.position === index) {
                imageUrl =  wardrobeDetail.image_url
                wardrobeImageDetail = wardrobeDetail
            }
        })
        if (imageUrl === imgEmpty) {
            return (
                <div className={'imageWrapper'}>
                    <img src={imageUrl} alt="imgProduct"/>
                </div>
            )
        } else {
            return (
                <div className={'image'} key={index}>
                    <img src={imageUrl} alt="imgProduct"/>
                    <div className={'btn-delete d-flex align-items-center justify-content-center'}>
                        <img src={icDelete} alt="delete" onClick={(e) => deleteImage(e, wardrobeImageDetail)}/>
                    </div>
                </div>
            )
        }
    }

    const uppy = new Uppy({
        id: 'uppy',
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'], maxNumberOfFiles: 1 }
    })

    uppy.use(thumbnailGenerator)

    uppy.on('thumbnail:generated', (file, preview) => {
        setBackgroundImage(preview)
        wardrobe.collection_url = preview
        wardrobe.collection_file = file.data
    })

    const setModalIsOpenToTrue = () => {
        setModalIsOpen(true)
    }
    const setModalIsOpenToFalse = () => {
        setModalIsOpen(false)
    }

    function handleSelectProduct(productSelect, productImage, index) {
        if (productSelect?.id) {
            const newWardrobeDetail = {image_url: productImage?.image_url, thumb_url: productImage?.thumb_url, product_id: productSelect?.id, position: index}
            let isCheck = true
            wardrobeDetails?.forEach(wardrobeDetail => {
                if (wardrobeDetail?.position === index) {
                    isCheck = false
                    wardrobeDetail.image_url  = productImage?.image_url
                    wardrobeDetail.thumb_url  = productImage?.thumb_url
                    wardrobeDetail.product_id  = productSelect.id
                }
            })
            if (isCheck) {
                wardrobeDetails.push(newWardrobeDetail)
            }
            setWardrobeDetails([...wardrobeDetails])
            wardrobe.wardrobe_details = wardrobeDetails
        }
    }

    function deleteBackgroundImage(e) {
        setBackgroundImage(null)
    }

    return (
        <Row className={'mb-5'}>
            <Col className={'col-12 m-auto'}>
                <Card className={'model'}>
                    <CardBody className={'wardrobe-contain'}>
                        <h2 className={'wardrobe-title'}>Tủ đồ</h2>
                        <div className={'wardrobe-grid'}>
                            <div className={'wardrobe-items'}>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(1)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(1)}
                                    </div>
                                </div>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(3)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(3)}
                                    </div>
                                    <div/>
                                </div>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(5)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(5)}
                                    </div>
                                    <div/>
                                </div>
                            </div>
                            <div className={'img-model'}>
                                {
                                    backgroundImage &&
                                    <>
                                        <img className={'image'} src={backgroundImage} alt="imgWardrobe"/>
                                        <div className={'btn-delete d-flex align-items-center justify-content-center'}>
                                            <img src={icDelete} alt="delete" onClick={(e) => deleteBackgroundImage(e)}/>
                                        </div>
                                    </>
                                }
                                {
                                    !backgroundImage &&
                                    <div className={`w-100 h-100 d-flex align-items-center justify-content-center image-empty`}>
                                        <img src={imgEmpty} alt="imgEmpty"/>
                                    </div>

                                }
                                <DragDrop uppy={uppy}/>
                            </div>
                            <div className={'wardrobe-items'}>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(2)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(2)}
                                    </div>
                                    <div/>
                                </div>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(4)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(4)}
                                    </div>
                                    <div/>
                                </div>
                                <div className={'wardrobe-item'}>
                                    <div className={'wardrobe-contain-img'} onClick={() => {
                                        setIndex(6)
                                        setModalIsOpenToTrue()
                                    }}>
                                        {convertImage(6)}
                                    </div>
                                    <div/>
                                </div>
                            </div>
                        </div>
                    </CardBody>
                </Card>
            </Col>
            <Modal isOpen={modalOpen} className='modal-dialog-centered' toggle={setModalIsOpenToFalse}>
                <ModalProduct closeModal={setModalIsOpenToFalse} handleSelectProduct={handleSelectProduct} products={products} index={index} />
            </Modal>
        </Row>
    )
}
export default WardrobeCollection
