// ** React Imports
import React, {useEffect, useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import '../styles/wardrobe.scss'
// ** Third Party Components
import Select from "react-select"
import {selectThemeColors} from "../../../../utility/Utils"
import {Col, Input, Label, Row} from "reactstrap"
import icDelete from '../../../../assets/images/icons/icons/ic_delete.svg'
import imgEmpty from "../../../../assets/images/icons/icons/ic_empty.svg"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import {DragDrop} from "@uppy/react"

const WardrobeModelPhoto = props => {
    // ** Props
    const { wardrobe } = props
    const [shirt_size, setShirtSize] = useState({})
    const [paint_size, setPainSize] = useState({})
    const [isLoad, setLoad] = useState(true)
    const [images, setImages] = useState([])

    useEffect(() => {
        if (wardrobe?.id && isLoad) {
            if (wardrobe &&  wardrobe?.shirt_size) {
                setShirtSize({label: wardrobe.shirt_size, value: wardrobe.shirt_size})
            }
            if (wardrobe &&  wardrobe?.pant_size) {
                setPainSize({label: wardrobe.pant_size, value: wardrobe.pant_size})
            }
            if (wardrobe &&  wardrobe?.wardrobe_images) {
                const images = []
                wardrobe?.wardrobe_images.forEach(wardrobe_image => {
                    images.push({thumb_url: wardrobe_image.thumb_url})
                })
                setImages([...images])
            }
            setLoad(false)
        }
    })

    const sizes = [
        {
            value: 'M',
            label: 'M'
        },
        {
            value: 'L',
            label: 'L'
        },
        {
            value: 'XL',
            label: 'XL'
        },
        {
            value: 'XXL',
            label: 'XXL'
        }
    ]

    function handleChangeText(value, type) {
        switch (type) {
            case 1:
                wardrobe.height = value
                break
            case 2:
                wardrobe.weight = value
                break
            case 3:
                wardrobe.width = value
                break
        }
    }

    function onChangeSize(data, number) {
        switch (number) {
            case 1:
                wardrobe.shirt_size = data.value
                setShirtSize(data)
                break
            case 2:
                wardrobe.pant_size = data.value
                setPainSize(data)
                break
        }

    }

    const uppy = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })
    uppy.use(thumbnailGenerator)

    uppy.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, thumb_url: preview}
        images.push(arr)
        setImages([...images])
        wardrobe.wardrobe_images = images
    })

    function deleteImage(event, image) {
        images.splice(images.indexOf(image), 1)
        setImages([...images])
        wardrobe.wardrobe_images = images
    }

    const uppy1 = new Uppy({
        id: 'uppy',
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*']}
    })

    uppy1.use(thumbnailGenerator)

    uppy1.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, thumb_url: preview}
        images.push(arr)
        setImages([...images])
        wardrobe.wardrobe_images = images
    })

    const renderPreview = () => {
        if (images.length) {
            return images?.map((image, index) => {
                return (
                    <div className={'image'} key={index}>
                        <img src={image?.thumb_url} alt="imgProduct" className={'img'}/>
                        <div className={'btn-delete d-flex align-items-center justify-content-center'}>
                            <img src={icDelete} alt="delete" onClick={(e) => deleteImage(e, image)}/>
                        </div>
                    </div>
                )
            })
        } else {
            return (
                <div className={'empty-image'}>
                    <div>
                        <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                        <p className={'empty-image-description'}>
                            Chưa có hình ảnh sản phẩm
                        </p>
                    </div>
                    <DragDrop uppy={uppy1}/>
                </div>
            )
        }
    }

    return (
        <div>
            <div>
                <Row className={'justify-content-between'}>
                    <Col md={5} sm={12}>
                        <h3 className={'wardrobe-info'}>Thông tin người mẫu</h3>
                        <div className={'wardrobe-product'}>
                            <div className={'d-flex align-items-center justify-content-sm-between input-contain'}>
                                <Label className={'name'}>Chiều cao</Label>
                                <Input type='number' name='height'
                                       className={'form-control wardrobe-represent--info-form-control'}
                                       defaultValue={wardrobe &&  wardrobe.height}
                                       onChange={e => handleChangeText(e.target.value, 1)}/>
                            </div>
                            <div className={'d-flex align-items-center justify-content-sm-between input-contain'}>
                                <Label className={'name'}>Cân nặng</Label>
                                <Input type='number' name='weight'
                                       className={'form-control wardrobe-represent--info-form-control'}
                                       defaultValue={wardrobe &&  wardrobe.weight}
                                       onChange={e => handleChangeText(e.target.value, 2)}/>
                            </div>
                            <div className={'d-flex align-items-center justify-content-sm-between input-contain'}>
                                <Label className={'name'}>Chiều rộng</Label>
                                <Input type='number' name='width'
                                       className={'form-control wardrobe-represent--info-form-control'}
                                       defaultValue={wardrobe &&  wardrobe.width}
                                       onChange={e => handleChangeText(e.target.value, 3)}/>
                            </div>
                        </div>
                    </Col>
                    <Col md={5} sm={12}>
                        <h3 className={'wardrobe-info'}>Thông tin áo quần</h3>
                        <div className={'wardrobe-product'}>
                            <div className={'d-flex align-items-center input-contain justify-content-between'}>
                                <Label className={'name'}>Size áo mẫu</Label>
                                <Select
                                    theme={selectThemeColors}
                                    isClearable={false}
                                    className='wardrobe-input option react-select'
                                    classNamePrefix='select'
                                    options={sizes}
                                    value={shirt_size}
                                    onChange={data => {
                                        onChangeSize(data, 1)
                                    }}
                                />
                            </div>
                            <div className={'d-flex align-items-center input-contain justify-content-between'}>
                                <Label className={'name'}>Size quần mẫu</Label>
                                <Select
                                    theme={selectThemeColors}
                                    isClearable={false}
                                    className='wardrobe-input option react-select'
                                    classNamePrefix='select'
                                    options={sizes}
                                    value={paint_size}
                                    onChange={data => {
                                        onChangeSize(data, 2)
                                    }}
                                />
                            </div>
                        </div>
                    </Col>
                </Row>
                <div className={'wardrobe--contain'}>
                    <div className={'d-flex justify-content-between align-items-center'} style={{marginBottom: '10px'}}>
                        <h3 className={'wardrobe-info mb-0'}>
                            Hình ảnh
                        </h3>
                        <div className={'product-detail--add-image'}>
                            <DragDrop uppy={uppy} locale={{ strings: {dropHereOr: "Thêm ảnh"} }}/>
                        </div>
                    </div>
                    <div className={'wardrobe--contain-image'}>
                        <div className={'d-flex h-100 align-items-center'}>
                            { renderPreview() }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default WardrobeModelPhoto
