import {Card, CardBody, Row, Col, Input} from "reactstrap"
import React, {useEffect, useState} from "react"
import imgEmpty from "../../../../assets/images/icons/icons/ic_upload_img.svg"
import '../styles/wardrobe.scss'
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import {DragDrop} from "@uppy/react"

const WardrobeInformation = (props) => {
    const {wardrobe} = props

    const [wardrobe_image, setWardrobeImage] = useState('')
    const [wardrobe_background_image, setWardrobeBackgroundImage] = useState(null)

    useEffect(() => {
        if (wardrobe?.id) {
            setWardrobeImage(wardrobe?.image_url ? wardrobe.image_url : '')
            setWardrobeBackgroundImage(wardrobe?.back_ground_url ? wardrobe.back_ground_url : '')
        }
    })

    const uppy = new Uppy({
        id: 'uppy',
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'], maxNumberOfFiles: 1 }
    })

    uppy.use(thumbnailGenerator)

    uppy.on('thumbnail:generated', (file, preview) => {
        setWardrobeImage(preview)
        wardrobe.image_url = preview
        wardrobe.image_file = file.data
    })

    const uppyBackground = new Uppy({
        id: 'uppy',
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'], maxNumberOfFiles: 1 }
    })

    uppyBackground.use(thumbnailGenerator)

    uppyBackground.on('thumbnail:generated', (file, preview) => {
        setWardrobeBackgroundImage(preview)
        wardrobe.back_ground_url = preview
        wardrobe.back_ground_file = file.data
    })

    function handleChangeName(value) {
        wardrobe.name = value
    }

    return (
        <Col className={`wardrobe col-xl-6 col-sm-12 w-100`}>
            <Row className='model wardrobe-represent w-100 h-100 bg-white p-2'>
                <Col className={'wardrobe-represent--img col-5'}>
                    {
                        wardrobe_image &&
                        <img className={'image'} src={wardrobe_image} alt="imgWardrobe"/>
                    }
                    {
                        !wardrobe_image &&
                        <div className={`w-100 h-100 d-flex align-items-center justify-content-center image-empty`}>
                            <img src={imgEmpty} alt="imgEmpty"/>
                        </div>

                    }
                    <DragDrop uppy={uppy}/>
                </Col>
                <Col className={'wardrobe-represent--info col-7'}>
                    <div>
                        <p className={'wardrobe-represent--info-title'}>Tên tủ đồ</p>
                        <Input type={'text'} placeholder={'Nhập tên tủ đồ'}
                               name={'name_wardrobe'}
                               className={'form-control wardrobe-represent--info-form-control'}
                               defaultValue={wardrobe &&  wardrobe.name}
                               onChange={e => handleChangeName(e.target.value)}
                        />
                    </div>
                    <div className={'wardrobe-represent--background'}>
                        <p className={'wardrobe-represent--info-title'}>Hình ảnh đại diện</p>
                        <div className={'w-100 wardrobe-represent--background-img'}>
                            {
                                wardrobe_background_image &&
                                <img className={'image'} src={wardrobe_background_image} alt="imgWardrobe"/>
                            }
                            {
                                !wardrobe_background_image &&
                                <div className={`w-100 h-100 d-flex align-items-center justify-content-center image-empty`}>
                                    <img src={imgEmpty} alt="imgEmpty"/>
                                </div>
                            }
                            <DragDrop uppy={uppyBackground}/>
                        </div>
                    </div>
                </Col>
            </Row>
        </Col>
    )
}

export default WardrobeInformation
