// ** React Imports
import React, {useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import '../../styles/modal-product.scss'
import AutoComplete from '@components/autocomplete'
import {
    Label, Button, CardBody, Input
} from 'reactstrap'
import classnames from "classnames"
import imgEmpty from "../../../../../assets/images/icons/icons/ic_upload_img.svg"
import currencyFormat from "../../../../../utility/CurrencyFormat"

const ModalProduct = props => {
    // ** Props
    const { handleSelectProduct, index, closeModal, products } = props

    const [productSelected, setProductSelected] = useState({})
    const [productImage, setProductImage] = useState('')
    const [value, setValue] = useState('')

    const onKeyDown = e => {
        if (e.keyCode === 13) {
            const value = e?.target?.value
            if (value) {
                const productSelect = products?.filter(product => product?.name?.toLowerCase().indexOf(value.toLowerCase()) > -1)?.[0]
                if (productSelect) {
                    setProductSelected(productSelect)
                    setValue('')
                }
            }
        }
    }

    return (
        <CardBody className={'card-product d-flex flex-column justify-content-between'}>
            <div>
                <AutoComplete
                    suggestions={products}
                    className='form-control position-relative'
                    filterKey='name'
                    placeholder="Tìm kiếm sản phẩm tương ứng với tủ đồ cần thiết lập..."
                    autoFocus={false}
                    defaultSuggestions
                    onKeyDown={onKeyDown}
                    value={value}
                    customRender={(suggestion, i, filteredData, activeSuggestion, onSuggestionItemClick, onSuggestionItemHover) => (
                        <li
                            className={classnames('suggestion-item', {
                                active: filteredData.indexOf(suggestion) === activeSuggestion
                            })}
                            key={i}
                            onMouseEnter={() => onSuggestionItemHover(filteredData.indexOf(suggestion))}
                            onClick={e => {
                                onSuggestionItemClick(null, e)
                                setProductSelected(suggestion)
                            }}
                        >
                            <div className={'suggestion-item-img'}>
                                <img src={suggestion?.product_images?.[0].thumb_url} alt={suggestion.name} height='60' width='60'/>
                            </div>
                            <span className={'suggestion-item-text'}>{suggestion.name}</span>
                        </li>
                    )}
                />
                {
                    productSelected?.id &&
                    <div className={'variants'}>
                        <div className={'product-card'}>
                            <div className={'product-card--img'}>
                                {
                                    productSelected?.product_images &&
                                    <img src={productSelected?.product_images?.[0].thumb_url} alt="imgProduct"/>
                                }
                                {
                                    !productSelected?.product_images &&
                                    <img src={imgEmpty} alt="imgProduct"/>
                                }
                            </div>
                            <div className={'product-card--info'}>
                                <div className={'product-card--info-item'}>
                                    <p>Mã sản phẩm: {productSelected?.id}</p>
                                    <p>Nhóm sản phẩm: {productSelected?.category}</p>
                                    <p>Số phiên bản: {productSelected?.product_variants?.length}</p>
                                </div>
                                <div className={'product-card--info-item'}>
                                    {
                                        productSelected?.price &&
                                        <p>Giá: {currencyFormat(productSelected?.price)}đ</p>
                                    }
                                    {/*<p>Màu: {productSelected}</p>*/}
                                    {/*<p>Số lượng: {'20'}</p>*/}
                                </div>
                            </div>
                        </div>
                        <div className={'variant-container'}>
                            {
                                productSelected?.product_images?.map((product_image, index) => {
                                    return (
                                        <div className={'variant-item'} key={index}>
                                            <img src={product_image?.thumb_url} alt="imgProduct"/>
                                            <div className={'radio-container'}
                                                 onChange={() => setProductImage(product_image)}>
                                                <Input type={'radio'} className={'radio'} name={'color'}/>
                                                <Label className={'radio-custom'}> </Label>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                }
                {
                    !productSelected?.id &&
                    <div></div>
                }
            </div>

            <div className={'d-flex justify-content-end'}>
                <Button.Ripple onClick={closeModal} color='dark' outline className={'btn btn-modal'}>
                    Hủy
                </Button.Ripple>
                <Button.Ripple color='dark' outline className={`btn btn-modal ${productSelected && productImage && 'active'}`} onClick={() => {
                    handleSelectProduct(productSelected, productImage, index)
                    closeModal()
                }}>
                    Chọn
                </Button.Ripple>
            </div>
        </CardBody>
    )
}

export default ModalProduct
